:root {
  --sic: hsl(40, 84%, 53%)
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.app{
  display: grid;
  place-content: center;
  align-items: center;
  background: radial-gradient(hsl(214, 47%, 23%) ,hsl(237, 49%, 15%));
  min-height: 100vh;
  min-width: 100vh;
  
}
.top{
  display: flex;
  justify-content: space-between;
  padding:27px 33px;
  width: 700px;
  border: 2px hsl(217, 16%, 45%) solid;
  border-radius: 13px;
}
.score{
  background-color: white;
  width: 150px;
  height: 114px;
  border-radius: 6px;
  display: grid;
  place-content: center;
  text-align: center;
}
.score p{
  font-size: 11px;
  color: hsl(229, 64%, 46%);
}
.score h2{
  font-size: 45px;
  color: hsl(229, 25%, 31%);
}
.game{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("./images/bg-triangle.svg");
  background-size: auto;
  background-repeat: no-repeat;
  background-position: center;
  margin-top: 46px;
}
.pap-sic{
  display: flex;
  justify-content: space-between;
  width: 390px;
  margin-bottom: 50px;
}
.paper{
  background: white;
  padding: 40px;
  border: 24px hsl(230, 89%, 65%) solid;
  border-radius: 50%;
  margin-right: 33px;
  cursor: pointer;
}

.sciccors{
  background: white;
  padding: 40px;
  border: 24px var(--sic) solid;
  border-radius: 50%;
  cursor: pointer;
}
.rock{
  background: white;
  padding: 40px;
  border: 24px hsl(349, 70%, 56%) solid;
  border-radius: 50%;
  cursor: pointer;
}
.answer{
   display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 46px;

}
.back{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.back h1{
  color: white;
}
.back button{
  width: 100%;
  color: red;
  padding: 5px;
  border-radius: 8px;
}